.loginform{
  gap: 1rem !important;
}

.keepSignedIn,
.registerNow{
  display: flex;
  font-size: var(--font-size-sm);
}
.keepSignedIn{
  justify-content: space-between;
  cursor: pointer;
  
}

.registerNow{
  justify-content: center;
}

.loginlink1{
  cursor: pointer;
  color: var(--color-orange);
}
.loginlink1:hover{
  font-weight: 550;
}
@media screen and (max-width: 400px) {
  .keepSignedIn{
    flex-flow: column nowrap;
  }
}